export const area_status_change = (status, area_key) => ({
	type: "area_status_change",
	status,
	area_key
});
export const area_item_add = (item, area_key) => ({
	type: "area_item_add",
	item,
	area_key
});
export const area_filter_add = (filter, area_key) => ({
	type: "area_filter_add",
	filter,
	area_key
});
export const area_filter_remove = (filter, area_key) => ({
	type: "area_filter_remove",
	filter,
	area_key
});



